.canvas {
  position: relative;
  width: 800px;
  height: 600px;
  border: 1px solid green;
}

.canvas > * {
  position: absolute;
  width: 800px;
  height: 600px;
  left: 0;
  top: 0;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

canvas {
  border: 1px solid tomato;
}

h2 {
  margin-bottom: 25px;
  margin-top: 45px;
}

body {
  padding: 40px;
}

.block {
  display: flex;
}
